.main-slider {
	margin-top: 20px;
	height: 480px;
	border-radius: 6px;
	box-shadow: 0 4px 18px 0 rgba(217, 211, 189, 0.49);
	box-sizing: border-box;
	overflow: hidden;
	background-color: $color-white;
	.slick-list {
		height: 100%;
	}
	.slick-track {
		height: 100%;
	}
	&__item {
		width: 100%;
		height: 100%;
		display: flex !important;
		justify-content: center;
		align-items: center;
		outline: none;
		a {
			width: 981px;
			height: 444px;
			display: flex;
			justify-content: center;
			align-items: center;
			text-decoration: none;
			outline: none;
		}
		img {
			max-width: 100%;
			max-height: 100%;
			outline: none;
		}
	}
	.slick-dots {
		bottom: 20px;
		li {
			width: 12px;
			height: 12px;
			button {
				width: 12px;
				height: 12px;
				background-color: #c6c6cf;
				border-radius: 50%;
				padding: 0;
				&::before {
					content: '';
					width: 8px;
					height: 8px;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					border-radius: 50%;
				}
			}
		}
		li.slick-active {
			button {
				background-color: rgba($color-red, 0.5);
				&::before {
					background-color: $color-red;
				}
			}
		}
	}
	&__arrow {
		position: absolute;
		top: 0;
		width: 27px;
		height: 100%;
		z-index: 10;
		display: flex;
		align-items: center;
		cursor: pointer;
		svg {
			width: 8.5px;
			height: 37.9px;
			opacity: 0.35;
			transition: opacity .3s;
		}
		&:hover {
			svg {
				opacity: 0.75;
			}
		}
		&--prev {
			left: 0;
			transform: rotate(180deg);
		}
		&--next {
			right: 0;
		}
	}
}
.internet-price {
	display: flex;
	justify-content: center;
	align-items: flex-start;
	margin-top: 106px;
	&__img {
		width: 350px;
		margin-left: 60px;
		margin-right: 70px;
	}
	&__txt {
		padding-right: 63px;
	}
	&__title {
		font-family: $font-firaM;
		font-size: 32px;
		font-weight: 500;
		line-height: 1.29;
		color: $color-txt1;
		margin: 0 0 36px;
	}
	&__list {
		counter-reset: internet-price;
	}
	&__item {
		padding-left: 46px;
		font-family: $font-fira;
		font-size: 21px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.33;
		letter-spacing: normal;
		color: $color-main2;
		margin-bottom: 18px;
		position: relative;
		&::before {
			content: counter(internet-price);
			counter-increment: internet-price;
			position: absolute;
			width: 24px;
			height: 24px;
			display: flex;
			justify-content: center;
			align-items: center;
			top: 0;
			left: 0;
			font-family: $font-fira;
			font-size: 15px;
			font-weight: bold;
			font-style: normal;
			font-stretch: normal;
			line-height: 0.8;
			letter-spacing: normal;
			color: $color-white;
			background-color: $color-red;
			border-radius: 50%;
		}
	}
}
.prime {
	margin-top: 100px;
	&__list {
		display: flex;
		justify-content: space-between;
	}
	&__item {
		width: 25%;
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
	}
	&__wrap {
		width: 96px;
		height: 96px;
		background-color: $color-f1e;
		border-radius: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&__par {
		margin-top: 18px;
		font-family: $font-fira;
		font-size: 18px;
		line-height: 1.33;
		color: $color-main2;
	}
}