// stylelint-disable no-empty-source
@font-face {
	font-family: 'FiraSans-medium';
	src: url('../css/fonts/FiraSans-Medium.woff2') format('woff2'),
	url('../css/fonts/FiraSans-Medium.woff') format('woff');
	font-weight: 500;
	font-style: normal;
}

@font-face {
	font-family: 'FiraSans';
	src: url('../css/fonts/FiraSans-Regular.woff2') format('woff2'),
	url('../css/fonts/FiraSans-Regular.woff') format('woff');
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: 'Slick';
	src: url('../css/fonts/slick.woff2') format('woff2'),
	url('../css/fonts/slick.woff') format('woff'),
	url('../css/fonts/slick.ttf') format('ttf');
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}