.overlay {
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100vw;
	height: 100vh;
	position: fixed;
	background-color: rgba($color-main2, 0.3);
	z-index: 111;
	opacity: 0;
	visibility: hidden;
	transition: opacity .3s, visibility .3s;
	&--active {
		opacity: 1;
		visibility: visible;
	}
}
.modal {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 112;
	border-radius: 6px;
	box-shadow: 0 4px 16px 0 rgba(38, 35, 74, 0.29);
	background-color: #ffffff;
	min-width: 400px;
	&--active {
		visibility: visible !important;
		opacity: 1 !important;
	}
	&__close {
		position: absolute;
		top: 10px;
		right: 10px;
	}
	&__wrap {
		display: flex;
		flex-direction: column;
		padding: 42px 56px 60px;
		align-items: center;
	}
	&__title {
		font-family: $font-firaM;
		font-size: 22px;
		font-weight: 500;
		text-align: center;
		color: $color-txt1;
		margin-bottom: 21px;
	}
	&__form {
		display: flex;
		flex-direction: column;
	}
	&__label {
		display: flex;
		flex-direction: column;
		&:first-child {
			margin-bottom: 23px;
		}
		span {
			text-align: center;
			font-family: $font-fira;
			font-size: 16px;
			line-height: 1.5;
			color: $color-main2;
			margin-bottom: 9px;
		}
		input {
			border-radius: 3px;
			max-width: 180px;
			background-color: #ffffff;
			text-decoration: none;
			border: 1px solid rgba($color-main2, 0.3);
			padding: 8px 16px 8px;
			font-family: $font-fira;
			font-size: 16px;
			line-height: 1.5;
			color: $color-main2;
			transition: border-color .3s, box-shadow .3s;
			outline: none;
			&:focus {
				border-color: $color-blue5e;
				box-shadow: 0px 0px 0px 4px rgba($color-blue5e, 0.3);
			}
		}
	}
	.link {
		text-align: center;
		color: #626260;
		line-height: 1.71;
		font-size: 14px;
		display: inline-block;
		max-width: 108px;
		margin: 7px auto 29px;
	}
	&__txt {
		font-size: 14px;
		line-height: 1.71;
		text-align: center;
		color: #626260;
		margin-bottom: 25px;
	}
	&__desc {
		font-size: 16px;
		line-height: 1.5;
		text-align: center;
		color: $color-main2;
		margin-bottom: 25px;
	}
	&__box {
		width: 100%;
		position: absolute;
		bottom: -35px;
		left: 0;
		text-align: center;
		font-size: 15px;
		line-height: 1.6;
		color: $color-main2;
		a {
			border-bottom: 1px solid rgba($color-main2, 0.3);
			transition: border-bottom-color .3s;
			&:hover {
				border-bottom-color: rgba($color-main2, 0.6);
			}
		}
	}
	&__check {
		input {
			position: absolute;
			z-index: -1;
			opacity: 0;
			margin: 10px 0 0 20px;
		}
	}
	&__check-text {
		font-size: 14px;
		line-height: 1.43;
		color: #626260;
		position: relative;
		padding: 0 0 0 28px;
		cursor: pointer;
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 16px;
			height: 16px;
			border-radius: 1px;
			background: $color-blue5e;
		}
		&::after {
			content: '';
			position: absolute;
			top: 3px;
			left: 2px;
			width: 12px;
			height: 10px;
			background: url("../images/sprites/svg/check.svg") center no-repeat;
			-webkit-background-size: cover;
			background-size: cover;
			opacity: 0;
			transition: opacity .3s;
		}
		a {
			font-size: 14px;
			line-height: 1.43;
			color: #626260;
			border-bottom: 1px solid rgba(#626260, 0.3);
			transition: border-bottom-color .3s;
			&:hover {
				border-bottom-color: rgba(#626260, 0.6);
			}
		}
	}
}
.modal__check input:checked + .modal__check-text::after {
	opacity: 1;
}
.user-modal {
	opacity: 0;
	visibility: hidden;
	transition: opacity .3s, visibility .3s;
}
.password-modal {
	opacity: 0;
	visibility: hidden;
	transition: opacity .3s, visibility .3s;
	.modal {
		&__label:first-child {
				margin-bottom: 0;
			}
	}
	&__prev {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 16px;
		width: 36px;
		height: 36px;
		background-color: rgba($color-main2, 0.1);
		border-radius: 50%;
		transition: background-color .3s;
		display: flex;
		justify-content: center;
		align-items: center;
		&:hover {
			background-color: rgba($color-main2, 0.2);
			svg use{
				fill: $color-main2;
			}
		}
	}
}
.ok-modal {
	opacity: 0;
	visibility: hidden;
	transition: opacity .3s, visibility .3s;
	.btn {
		width: 110px;
		padding-top: 8px;
		padding-bottom: 8px;
	}
}
.registration-modal {
	min-width: 620px;
	opacity: 0;
	visibility: hidden;
	transition: visibility .3s, opacity .3s;
	.modal__wrap {
		align-items: flex-start;
	}
	.modal__title {
		text-align: center;
		margin-left: auto;
		margin-right: auto;
	}
	.modal__label {
		flex-direction: row;
		align-items: center;
		margin-bottom: 28px;
		&:first-child {
			margin-bottom: 28px;
		}
		span {
			width: 100px;
			margin-right: 48px;
			text-align: left;
			margin-bottom: 0;
		}
	}
	.btn {
		width: 180px;
		margin-left: 148px;
	}
	.modal__check {
		margin-left: 148px;
		margin-top: 21px;
	}
}