.fixed-btn-up {
	position: fixed;
	right: 11px;
	bottom: 11px;
	border-radius: 3px;
	box-shadow: 0 1px 4px 0 rgba($color-red, 0.63);
	background-color: rgba($color-red, 0.8);
	z-index: 120;
	width: 40px;
	height: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
	visibility: hidden;
	opacity: 0;
	transition: background-color .3s, opacity .6s, visibility .6s;
	cursor: pointer;
	&--active {
		opacity: 1;
		visibility: visible;
	}
	svg {
		width: 14px;
		height: 8px;
		use {
			fill: rgba($color-white, 0.8);
			transition: fill .3s;
		}
	}
	&:hover {
		background-color: rgba($color-red, 1);
		svg use {
			fill: rgba($color-white, 1);
		}
	}
}