// stylelint-disable scss/dollar-variable-empty-line-before

// COLORS

$color-black: #000;
$color-white: #fff;
$color-bg: #f8f5ea;
$color-f1e: #f1edde;
$color-red: #d0271e;
$color-main2: #292930;
$color-txt1: #1e1e22;
$color-blue5: #393185;
$color-blue7: #393185;
$color-blue5e: #393185;

// FONTS
$font-fira: 'FiraSans', 'Arial', sans-sarif;
$font-firaM: 'FiraSans-medium', 'Arial', sans-sarif;

// breakpoints
$container-xl: 1075px;
$sm: 576px;
$md: 768px;
$lg: 992px;
$xl: 1200px;

// for mobile

$screen-xs-max: 1199px;

