.title {
	font-family: $font-firaM;
	font-size: 32px;
	font-weight: 500;
	text-align: center;
	color: $color-txt1;
	margin: 70px 0 36px;
}
.title--desc {
	margin-bottom: 11px;
}
.title-left {
	margin: 70px 0 36px;
	display: block;
	font-family: $font-firaM;
	font-size: 32px;
	font-weight: 500;
	color: $color-txt1;
}
.subtitle {
	margin: 60px 0 47px;
	font-family: $font-firaM;
	font-size: 34px;
	font-weight: 500;
	color: $color-txt1;
}
.desc {
	font-family: $font-fira;
	font-size: 20px;
	line-height: 1.4;
	text-align: center;
	color: $color-txt1;
	margin: 0;
}
.btn {
	border-radius: 3px;
	background-color: $color-white;
	border: 1px solid rgba($color-main2, 0.3);
	font-family: $font-fira;
	font-size: 15px;
	line-height: 1.6;
	text-align: center;
	color: $color-main2;
	padding: 4px 24px;
	cursor: pointer;
	transition: background-color .3s, color .3s, box-shadow .3s, border-color .3s;
	text-decoration: none;
}
.btn-red {
	box-shadow: 0 1px 4px 0 rgba($color-red, 0.63);
	background-color: $color-red;
	border-color: $color-red;
	font-size: 16px;
	line-height: 1.5;
	color: $color-white;
	padding-top: 12px;
	padding-bottom: 12px;
}
.btn-reds {
	border-radius: 3px;
	box-shadow: 0 1px 4px 0 rgba(230, 72, 64, 0.63);
	background-color: $color-red;
	padding: 4px 16px;
	line-height: 1.6;
	color: $color-white;
	font-size: 15px;
	border: 1px solid $color-red;
	cursor: pointer;
	span {
		line-height: 1.6;
	}
}
.link-solid-black {
	border-bottom: 1px solid rgba($color-main2, 0.3);
	transition: border-bottom-color .3s;
	&:hover{
		border-bottom-color: rgba($color-main2, 0.6);
	}
}
.link-solid-blue {
	border-bottom: 1px solid rgba($color-blue5e, 0.3);
	transition: border-bottom-color .3s;
	&:hover{
		border-bottom-color: rgba($color-blue5e, 0.6);
	}
}
.link-solid-blue-s {
	span {
		border-bottom: 1px solid rgba($color-blue5e, 0.3);
		transition: border-bottom-color .3s;
	}
	&:hover span{
		border-bottom-color: rgba($color-blue5e, 0.6);
	}
}
.link-solid-black-s {
	span {
		border-bottom: 1px solid rgba($color-main2, 0.3);
		transition: border-bottom-color .3s;
	}
	&:hover span{
		border-bottom-color: rgba($color-main2, 0.6);
	}
}
.link-dashed-s {
	border: none;
	font-family: $font-fira;
	cursor: pointer;
	background-color: transparent;
	span {
		border-bottom: 1px dashed rgba($color-main2, 0.3);
		transition: border-bottom-color .3s;
	}
	&:hover span{
		border-bottom-color: rgba($color-main2, 0.6);
	}
}
.link {
	display: inline-block;
	font-family: $font-fira;
	font-size: 14px;
	color: $color-main2;
	cursor: pointer;
	span {
		border-bottom: 1px dashed rgba($color-main2, 0.3);
		transition: border-bottom-color .3s;
	}
	&:hover span{
		border-bottom-color: rgba($color-main2, 0.6);
	}
}