.offers {
	margin-top: 24px;
	display: flex;
	&__list {
		display: flex;
		width: 80%;
		flex-flow: wrap;
		overflow: hidden;
		max-height: 28px;
		transition: max-height .6s;
		&--active {
			max-height: 200px;
		}
	}
	&__link {
		border-radius: 12px;
		background-color: transparent;
		font-size: 14px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		color: #292930;
		padding: 3px 8px 4px;
		display: flex;
		margin-right: 8px;
		margin-bottom: 8px;
		border: 1px solid $color-red;
	}
	.link {
		margin-top: 10px;
	}
}