*,
*::before,
*::after {
	box-sizing: inherit;
}
html,
body {
	height: 100%;
	font-size: 16px;
	font-family: $font-fira;
	background: $color-bg;
	color: $color-main2;
}

html {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	overflow-y: scroll;
}
body {
	width: auto;
	margin: 0;
	padding: 0;
}
ul {
	padding: 0;
	margin: 0;
	list-style: none;
}
a {
	text-decoration: none;
	color: $color-main2;
}
form {
	margin: 0;
}
.container {
	margin-right: auto;
	margin-left: auto;
	padding: 0 30px;
}
.overflow-y-hidden {
	overflow-y: hidden;
}
.wrapper {
	display: flex;
	flex-direction: column;
	height: 100%;
}
.content {
	flex: 1 0 auto;
}
.footer {
	flex: 0 0 auto;
}
@media (min-width: 768px) {
	.container {
		width: 750px;
		padding: 0;
	}
}

@media (min-width: 992px) {
	.container {
		width: 970px;
		padding: 0;
	}
}

@media (min-width: 1150px) {
	.container {
		width: 1075px;
		padding: 0;
	}
}