.product-slide {
	&__wrapper {
		position: relative;
		min-height: 400px;
		margin-top: 30px;
	}
	position: absolute;
	top: 0;
	left: 0;
	width: 1075px;
	height: 550px;
	z-index: 12;
	display: flex;
	justify-content: space-between;
	flex-shrink: 0;
	box-sizing: border-box;
	outline: none;
	&__wrap {
		position: relative;
		width: 250px;
		min-height: 348px;
		margin-right: 25px;
		box-sizing: border-box;
		outline: none;
		z-index: 1;
		transition-delay: 0.6s;
	}
	&__item {
		position: absolute;
		top: 0;
		left: 0;
		border-radius: 4px;
		background-color: $color-white;
		width: 100%;
		padding: 24px 20px 32px;
		box-sizing: border-box;
		transition: box-shadow .3s;
		z-index: 1;
	}
	&__like {
		position: absolute;
		top: 16px;
		left: 16px;
		z-index: 20;
		cursor: pointer;
		svg {
			width: 18px;
			height: 16px;
			use {
				fill: #dddde5;
				transition: fill .3s;
			}
		}
		&:hover {
			svg use {
				fill: #e64840;
			}
		}
	}
	&__top {
		width: 210px;
		height: 160px;
		box-sizing: border-box;
		display: flex;
		position: relative;
		z-index: 0 !important;
		&-item {
			width: 100%;
			height: 160px;
			display: flex !important;
			justify-content: center;
			align-items: center;
			position: relative;
			z-index: -1 !important;
			img {
				position: relative;
				z-index: -1 !important;
				max-width: 210px;
				max-height: 160px;
			}
		}
	}
	&__bottom {
		display: flex;
		justify-content: space-between;
		width: 100%;
		margin-bottom: 18px;
		max-height: 0;
		transition: max-height .3s;
		&-item {
			height: 56px;
			width: 56px;
			box-sizing: border-box;
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 4px;
			border: 1px solid rgba($color-main2, 0.3);
			outline: none;
			cursor: pointer;
			transition: border-color .3s;
			&:hover {
				border: 1px solid $color-blue5;
			}
			img {
				max-width: 54px;
				max-height: 54px;
			}
		}
		&-item.slick-active.slick-slide.slick-current {
			border: 1px solid $color-blue5;
		}
	}
	&__arrow {
		position: absolute;
		top: 21px;
		width: 62px;
		height: 20px;
		z-index: 10;
		display: flex;
		align-items: center;
		cursor: pointer;
		visibility: hidden;
		opacity: 0;
		transition: opacity .3s, visibility .3s;
		svg {
			width: 14px;
			height: 8px;
			opacity: 0.35;
			transition: opacity .3s, fill .3s;
		}
		&--prev {
			left: -40px;
			justify-content: center;
			transform: rotate(-90deg);
		}
		&--next {
			right: -40px;
			justify-content: center;
			transform: rotate(90deg);
		}
		&:hover {
			svg {
				opacity: 1;
				fill: $color-blue5;
			}
		}
	}
	&--right {
		position: absolute;
		top: 16px;
		right: 0;
		width: 81px;
		height: 24px;
		display: flex;
		justify-content: center;
		align-items: center;
		z-index: 20;
		&::after {
			content: '';
			position: absolute;
			top: -3px;
			left: -3px;
			width: 5px;
			height: 15px;
			background-color: $color-white;
			transform: rotate(15deg);
		}
		&::before {
			content: '';
			position: absolute;
			bottom: -3px;
			left: -3px;
			width: 5px;
			height: 15px;
			background-color: $color-white;
			transform: rotate(-15deg);
		}
	}
	&__new {
		font-family: $font-firaM;
		font-size: 14px;
		font-weight: 500;
		color: $color-white;
		background-color: $color-blue5e;
	}
	&__sale {
		font-family: $font-firaM;
		font-size: 14px;
		font-weight: 500;
		color: $color-white;
		background-color: $color-red;
	}
	&__title {
		display: inline-block;
		font-family: $font-fira;
		font-size: 17px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.18;
		letter-spacing: normal;
		color: $color-main2;
		border-bottom: 1px solid rgba($color-main2, 0.3);
		transition: color .3s, border-bottom-color .3s;
		margin-bottom: 17px;
	}
	&__desc {
		max-height: 0;
		opacity: 0;
		visibility: hidden;
		transition: opacity .2s, visibility .2s, max-height .3s;
	}
	&__desc > span {
			display: flex;
			font-family: $font-fira;
			font-size: 15px;
			font-weight: normal;
			font-style: normal;
			font-stretch: normal;
			line-height: 1.33;
			letter-spacing: normal;
			color: $color-main2;
			margin-bottom: 6px;
	}
	&__size {
		margin: 0 0 14px 0;
		padding: 0;
		display: flex;
		&-link {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 25px;
			height: 25px;
			font-size: 15px;
			font-weight: normal;
			font-style: normal;
			font-stretch: normal;
			letter-spacing: normal;
			border-radius: 50%;
			&--active {
				background-color: $color-blue7;
				color: $color-white;
			}
		}
	}
	&__price {
		font-size: 19px;
		line-height: 1.26;
		margin-bottom: 14px;
		span {
			margin-right: 17px;
		}
		.product-slide__no-price {
			font-size: 17px;
			line-height: 1.41;
			color: $color-red;
			text-decoration: line-through;
		}
	}
	&__box {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
	}
	&__btn {
		margin-top: 16px;
	}
	&__link {
		border-bottom: 1px dashed rgba($color-main2, 0.3);
		cursor: pointer;
		transition: border-bottom-color .3s;
		&:hover {
			border-bottom-color: rgba($color-main2, 0.6);
		}
	}
}
.product {
	&__arrow {
		position: absolute;
		display: flex;
		top: -18px;
		height: 445px;
		width: 35px;
		justify-content: flex-end;
		box-shadow: -9px 0 8px -8px rgba(19, 18, 30, 0.16);
		background-color: $color-bg;
		align-items: center;
		cursor: pointer;
		z-index: 20;
		svg {
			width: 20px;
			height: 15px;
			use {
				fill: rgba($color-main2, 0.3);
				transition: fill .3s;
			}
		}
		&--next {
			right: -35px;
		}
		&--prev {
			left: -35px;
			transform: rotate(180deg);
		}
		&:hover {
			svg use {
				fill: rgba($color-main2, 0.6);
			}
		}
	}
}
.product-slide__wrap:hover {
	z-index: 2;
	transition-delay: 0s;
	.product-slide {
		&__item {
			box-shadow: 0 4px 18px 0 rgba(217, 211, 189, 0.49);
		}
		&__bottom {
			max-height: 100px;
		}
		&__arrow {
			opacity: 1;
			visibility: visible;
			transition-delay: .3s;
		}
		&__desc {
			max-height: 100px;
			opacity: 1;
			visibility: visible;
			transition: opacity .8s, visibility .8s, max-height .3s;
		}
		&__title {
			color: $color-blue5;
			border-bottom-color: rgba($color-blue5, 0.3);
			transition: border-bottom-color .3s;
			&:hover {
				border-bottom-color: rgba($color-blue5, 0.6);
			}
		}
		&__btn {
			box-shadow: 0 1px 4px 0 rgba(230, 72, 64, 0.63);
			background-color: $color-red;
			border-color: $color-red;
			color: $color-white;
		}
	}
}