.nav-menu {
	display: flex;
	border-radius: 4px;
	box-shadow: 0 2px 6px 0 rgba(73, 71, 63, 0.3);
	background-color: $color-red;
	height: 60px;
	align-items: center;
	justify-content: space-between;
	position: relative;
	transition: border-radius .3s;
	&--active {
		border-bottom-left-radius: 0;
		border-bottom-right-radius: 0;
	}
	&--fixed {
		position: fixed;
		border-top-left-radius: 0;
		border-top-right-radius: 0;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		width: 100%;
		z-index: 100;
		max-width: $container-xl;
	}
	&__logo {
		padding-left: 20px;
		padding-right: 22px;
		height: 100%;
		display: flex;
		align-items: center;
	}
	&__list {
		display: flex;
		height: 100%;
		li {
			height: 100%;
		}
	}
	&__link {
		display: flex;
		align-items: center;
		height: 100%;
		font-family: $font-firaM;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		letter-spacing: 0.6px;
		text-transform: uppercase;
		color: $color-white;
		padding: 0 16px;
		transition: color .5s, background-color .5s;
		outline: none;
		&--active {
			background-color: $color-white;
			color: $color-red;
			svg {
				use {
					fill: $color-red;
					stroke: $color-red;
				}
			}
		}
		svg {
			margin-left: 10px;
			use {
				transition: fill .5s, stroke .5s;
			}
		}
	}
	&__box {
		display: flex;
		height: 100%;
		a {
			display: flex;
			height: 100%;
			align-items: center;
			font-family: $font-firaM;
			font-weight: 500;
			font-style: normal;
			font-stretch: normal;
			letter-spacing: normal;
			color: #ffffff;
			padding: 0 20px;
			position: relative;
			transition: background-color .3s, color .3s;
			&::before {
				content: '';
				position: absolute;
				top: 8px;
				left: 0;
				width: 1px;
				height: 44px;
				background-color: #ce3831;
				transition: background-color .3s;
			}
			&:hover {
				background-color: $color-white;
				color: $color-red;
				&::before {
					background-color: $color-white;
				}
				svg use {
					fill: $color-red;
				}
				.nav-menu__shop {
					background-color: $color-red;
					use {
						fill: $color-white;
					}
				}
			}
		}
		.svg-like {
			margin-right: 10px;
		}
	}
	&__shop {
		width: 32px;
		height: 32px;
		background-color: $color-white;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		margin-left: 10px;
		transition: background-color .3s;
		use {
			transition: fill .3s;
		}
	}
	&__search {
		height: 40px;
		width: 42px;
		display: flex;
		align-items: center;
		margin-right: 36px;
		padding-left: 16px;
		position: relative;
		z-index: 100;
		&-wrap {
			position: absolute;
			top: 0;
			right: 0;
			width: 280px;
			height: 40px;
			overflow: hidden;
			pointer-events: none;
		}
		&-block {
			position: absolute;
			width: 280px;
			height: 40px;
			display: flex;
			align-items: center;
			top: 0;
			left: 245px;
			transition: background-color 0.4s, border-radius 0.4s, left 0.4s;
			transition-delay: 0.3s;
		}
		.search {
			display: flex;
			align-items: center;
			height: 100%;
			&__svg {
				position: absolute;
				left: 0;
				top: 0;
				height: 100%;
				display: flex;
				align-items: center;
				border-radius: 20px 0 0 20px;
				transition: all 0.4s;
				transition-delay: 0.3s;
			}
		}
		.svg-search {
			margin-left: 16px;
			margin-right: 10px;
			transition: width 0.3s, height 0.3s;
			use {
				transition: fill 0.3s;
			}
		}
		input {
			outline: none;
			border: none;
			background-color: transparent;
			font-family: $font-fira;
			font-size: 16px;
			font-weight: normal;
			font-style: normal;
			font-stretch: normal;
			line-height: normal;
			letter-spacing: normal;
			color: $color-main2;
			max-width: 182px;
			padding-left: 30px;
			text-align: right;
			opacity: 0;
			transition: opacity .7s;
		}
		input[type="text"]::placeholder {
			padding-left: 8px;
			text-align: left;
		}
		button {
			background: transparent;
			border: none;
			position: absolute;
			right: 0;
			top: 0;
			height: 100%;
			border-radius: 0 20px 20px 0;
			padding: 0 14px;
			font-family: $font-fira;
			font-size: 16px;
			font-weight: normal;
			font-style: normal;
			font-stretch: normal;
			line-height: normal;
			letter-spacing: normal;
			color: $color-main2;
			transition: background-color 0.3s;
			box-sizing: border-box;
			&::before {
				content: '';
				position: absolute;
				top: 6px;
				left: 0;
				height: 28px;
				width: 1px;
				background-color: #dadada;
				transition: top 0.3s, height 0.3s;
			}
			&:hover {
				background-color: rgba(#dadada, 0.3);
				&::before {
					top: 0;
					height: 100%;
				}
			}
		}
		&--active {
			.nav-menu__search {
				&-wrap {
					width: 280px;
					pointer-events: auto;
				}
				&-block {
					left: 0;
					background-color: $color-white;
					border-radius: 20px;
				}
			}
			.search {
				&__svg {
					background-image: linear-gradient(to right, #ffffff, #ffffff, #ffffff, #ffffff, rgba(#ffffff, 0.2));
				}
				input {
					opacity: 1;
				}
			}
			.svg-search {
				width: 12px;
				height: 12px;
				use {
					fill: $color-main2;
				}
			}
		}
	}
}