.bx-filter {
	border-radius: 4px;
	background-color: #ffffff;
	border: 1px solid rgba($color-main2, 0.3);
	padding: 11px 0 16px;
}
.bx-filter-block {
	display: none;
}
.smartfilter__box {
	display: flex;
	flex-wrap: wrap;
	padding: 0 23px 16px;
	border-bottom: 1px solid rgba($color-main2, 0.3);
}
.bx-filter-parameters-box {
	width: 16.66%;
	padding: 10px 0;
	margin-bottom: 10px;
	position: relative;
	cursor: pointer;
}
.bx-filter-button-box {
	padding: 11px 23px 0;
}
.bx-filter-parameters-box-title {
	display: flex;
	align-items: center;
	span {
		margin-right: 8px;
		border-bottom: 1px dashed rgba($color-main2, 0.3);
		transition: border-bottom-color .3s;
	}
	&:hover span{
		border-bottom-color: rgba($color-main2, 0.6);
	}
}