.brand {
	&__title {
		font-family: $font-firaM;
		font-size: 14px;
		font-weight: 500;
		color: $color-main2;
		margin-bottom: 9px;
	}
	&__box {
		margin-bottom: 23px;
	}
	&__list {
		display: flex;
		flex-wrap: wrap;
	}
	&__link {
		display: block;
		margin-right: 17px;
		color: rgba($color-txt1, 0.9);
		margin-bottom: 10px;
	}
	&__par {
		max-width: 800px;
		font-family: $font-fira;
		font-size: 13px;
		line-height: 1.54;
		color: rgba($color-main2, 0.9);
	}
	&__item {
		padding-left: 10px;
		font-size: 13px;
		line-height: 1.54;
		color: rgba($color-main2, 0.9);
		margin-top: 10px;
		position: relative;
		&::before {
			content: '';
			position: absolute;
			top: 50%;
			left: 3px;
			transform: translateY(-50%);
			width: 3px;
			height: 3px;
			background-color: rgba($color-main2, 0.9);
			border-radius: 50%;
		}
	}
}