.fast-catalog {
	display: flex;
	justify-content: space-between;
	margin-top: 37px;
	&__wrapper {
		width: 49%;
		border-radius: 6px;
		-webkit-background-size: cover;
		background-size: cover;
		padding: 28px 32px 46px;
		min-height: 296px;
		overflow: hidden;
		position: relative;
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(#2d2b23, 0.7);
			z-index: 0;
		}
		a {
			position: relative;
			z-index: 1;
			span {
				font-family: $font-fira;
				font-size: 17px;
				line-height: 1.41;
				color: $color-white;
				border-bottom: 1px solid rgba($color-white, 0.3);
				transition: border-bottom-color .3s;
			}
			&:hover {
				span {
					border-bottom-color: rgba($color-white, 0.6);
				}
			}
		}
	}
	a.fast-catalog__title {
		span {
			display: inline-block;
			margin-bottom: 15px;
			font-size: 23px;
			font-family: $font-firaM;
			font-weight: 600;
		}
	}
	&__list {
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;
		align-content: start;
		max-height: 180px;
		li {
			display: block;
			min-width: 120px;
			margin-right: 34px;
			a {
				display: flex;
				padding: 5px 0;
				justify-content: flex-start;
				align-items: flex-start;
				span {

				}
			}
		}
	}
	&__wrapper--woman {
		background-image: url("../images/shoes_woman_2x.jpg");
	}
	&__wrapper--man {
		background-image: url(../images/shoes_man_2x.jpg);
	}
	&__wrapper--bags {
		background-image: url("../images/shoes_bags_2x.jpg");
	}
	&__wrapper--certificate {
		background-image: url(../images/shoes_certificate_2x.jpg);
	}
	&__box:first-child {
		width: 660px;
		.fast-catalog__wrapper {
			max-height: 296px;
		}
	}
	&__box:last-child {
		width: 390px;
		.fast-catalog__wrapper:first-child {
			height: 456px;
		}
		.fast-catalog__wrapper:last-child {
			height: 136px;
			min-height: 136px;
			max-height: 136px;
		}
		.fast-catalog__list {
			max-height: 400px;
		}
	}
	&__box {
		.fast-catalog__wrapper {
			width: 100%;
			margin-bottom: 28px;
		}
	}
}