.footer {
	margin-top: 88px;
	&__wrap {
		border-radius: 6px;
		background-color: $color-f1e;
		padding: 23px 33px 40px;
	}
	&__list {
		display: flex;
		margin-bottom: 28px;
	}
	&__link {
		font-family: $font-fira;
		font-size: 16px;
		line-height: 1.5;
		color: $color-main2;
		margin-right: 19px;
		border-bottom: 1px solid rgba($color-main2, 0.3);
		transition: border-bottom-color .3s;
		&:hover {
			border-bottom-color: rgba($color-main2, 0.6);
		}
	}
	&__box {
		display: flex;
	}
	&__md {
		font-family: $font-firaM;
		font-size: 15px;
		font-weight: 500;
		line-height: 1.33;
		color: $color-main2;
	}
	&__block {
		&:last-child {
			margin-left: 119px;
		}
		&-tel {
			position: relative;
			padding-right: 70px;
			padding-bottom: 15px;
			margin-right: 72px;
			.footer__md {
				padding: 0 10px;
				background-color: $color-f1e;
				margin-left: 18px;
				position: relative;
				z-index: 1;
			}
			&::before {
				content: '';
				position: absolute;
				top: 10px;
				left: 0;
				width: 100%;
				height: 100%;
				border-radius: 3px;
				border: 1px solid #E0DAC5;
				z-index: 0;
			}
		}
	}
	&__tel {
		display: block;
		font-family: $font-firaM;
		font-size: 18px;
		font-weight: 500;
		line-height: 1.33;
		color: $color-main2;
		margin: 13px 0 1px 28px;
	}
	&__time {
		display: block;
		font-family: $font-fira;
		font-size: 15px;
		line-height: 1.6;
		color: rgba($color-main2, 0.8);
		margin-left: 28px;
	}
	&__par {
		font-family: $font-fira;
		font-size: 15px;
		line-height: 1.6;
		color: $color-main2;
		max-width: 284px;
		margin: 0;
		a {
			color: $color-red;
			border-bottom: 1px solid rgba($color-red, 0.3);
			transition: border-bottom-color .3s;
			&:hover {
				border-bottom-color: rgba($color-red, 0.6);
			}
		}
	}
	&__item {
		margin-right: 16px;
	}
	&__items {
		margin-top: 15px;
		display: flex;
	}
}
.copy {
	display: flex;
	padding: 11px 0 13px;
	justify-content: space-between;
	&__block {
		display: flex;
		font-family: $font-fira;
		font-size: 14px;
		color: rgba($color-txt1, 0.8);
		&:last-child {
			margin-right: 20px;
			svg {
				margin-right: 14px;
			}
		}
	}
	&__par {
		margin: 0;
	}
	&__link {
		margin-left: 18px;
		border-bottom: 1px solid rgba($color-txt1, 0.3);
	}
}