.doggle-menu {
	position: absolute;
	top: 60px;
	left: 0;
	width: $container-xl;
	z-index: 101;
	background-color: $color-white;
	border-radius: 0 0 3px 3px;
	box-shadow: 0 8px 16px 0 rgba(38, 35, 74, 0.29);
	overflow: hidden;
	visibility: hidden;
	opacity: 0;
	&--active {
		opacity: 1;
		visibility: visible;
	}
	&__wrap {
		padding: 0 32px 43px;
		width: 100%;
	}
	&__cattalog {
		display: flex;
		padding-top: 28px;
		position: relative;
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: -32px;
			width: $container-xl;
			height: 1px;
			background-color: #e1e1e2;
		}
	}
	&__categories {
		margin-right: 7px;
	}
	&__list {
		max-height: 440px;
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;
		align-content: start;
		li {
			display: block;
			min-width: 145px;
			margin-right: 25px;
		}
	}
	&__title {
		font-family: $font-firaM;
		font-size: 16px;
		font-weight: bold;
		color: $color-txt1;
		margin-bottom: 12px;
	}
	&__item {
		display: flex;
		height: 100%;
		font-family: $font-fira;
		font-size: 15px;
		line-height: 1.33;
		color: $color-txt1;
		padding: 6px 0;
		span {
			border-bottom: 1px solid rgba($color-txt1, 0.3);
			transition: border-bottom-color .3s;
		}
		&:hover {
			span {
				border-bottom-color: rgba($color-txt1, 0.6);
			}
		}
	}
	.offers {
		margin-top: 20px;
		padding-bottom: 20px;
	}
}