.svg {
	&-user {
		width: 10px;
		height: 12px;
		use {
			fill: $color-main2;
		}
	}
	&-logo {
		width: 168px;
		height: 40px;
	}
	&-search {
		width: 20px;
		height: 20px;
		use {
			fill: $color-white;
		}
	}
	&-arrow {
		width: 10px;
		height: 5.6px;
		use {
			fill: $color-white;
			stroke: $color-white;
		}
	}
	&-like {
		width: 18px;
		height: 16px;
		use {
			fill: $color-white;
			transition: fill .3s;
		}
	}
	&-shop {
		width: 16px;
		height: 14px;
		use {
			fill: $color-red;
		}
	}
	&-sale {
		width: 44px;
		height: 44px;
	}
	&-prime {
		width: 60px;
		height: 48px;
		use {
			fill: $color-blue5e;
		}
	}
	&-social {
		width: 32px;
		height: 32px;
	}
	&-mmit {
		height: 21.5px;
		width: 90px;
	}
	&-close {
		width: 10px;
		height: 10px;
	}
	&-arrow_6 {
		width: 15px;
		height: 12px;
		use {
			transition: fill .3s;
		}
	}
	&-filter {
		width: 10px;
		height: 6px;
		use {
			fill: $color-main2;
		}
	}
}